<template>
  <div>
    <!---->
    <router-link class="link" to="/target">
      <button class="btn">Path()</button>
    </router-link>
    <router-link class="link" to="/target/123">
      <button class="btn">Path()</button>
    </router-link>
    <router-link class="link" to="/target/123/">
      <button class="btn">Path(,)</button>
    </router-link>

    <button class="btn" v-for="(info,index) in btnArray" :key="index" @click="info.handler">{{info.title}}</button>
  </div>
</template>

<script>
  export default {
    name: "home",

    data () {
      return {
        btnArray: [{title: 'Path()', handler: this.action1},
                   {title: 'name()', handler: this.action2},
                   {title: 'name(,)', handler: this.action3},
                   {title: 'path()', handler: this.action4},
                   {title: '()', handler: this.action5},
                   {title: 'AlloyFinger demo', handler: this.action6},
                   {title: '', handler: this.action7},
                   {title: 'demo', handler: this.action8},
                   {title: 'AsyncDemo', handler: this.action9}]
      }
    },

    methods: {
      action1() {
        this.$router.push({
          name: 'Target'
        })
      },

      action2() {
        this.$router.push({
          name: 'target',
          params: {userId: '1234'}
        })
      },

      action3() {
        this.$router.push({
          name: 'target',
          params: {userId: '1234',userName: ''}
        })
      },

      action4() {
        this.$router.push({
          path: '/target/childTarget'
        })
      },

      action5(){
        this.$router.push({
          name: 'ScrollDemo'
        })
      },

      action6() {
        this.$router.push({
          name: 'AlloyFingerDouble'
        })
      },

      action7 () {
        this.$router.push({
          name: 'Skin'
        })
      },

      action8 () {
        this.$router.push({
          name: 'PluginDemo'
        })
      },

      action9 () {
        this.$router.push({
          name: 'AsyncDemo'
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../assets/css/home.scss";

  .container {
    height: 100vh;
    width: 100vw;
    overflow: scroll;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
  }
</style>



